	module.exports = {"pathContext":{"group":[{"node":{"id":"30b7f9bf-4f55-5060-9612-7577ad59c384","slug":"editors-picks-2015-part-three","title":"Editors’ Picks 2015: Part Three","excerpt":"<p>Combating Dissemination Bias in Clinical Research: Recommendations Tip of the iceberg: Dissemination bias includes publication bias, where published papers that are exposed to readers are biased in favour of particular characteristics (e.g. positive findings). Papers with other characteristics (e.g. negative findings) are not published and, as a result, remain out of view from readers. &nbsp; [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/06/editors-picks-2015-part-three/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 06, 2016","template":"","format":"standard"}},{"node":{"id":"68f911b9-e2d0-5532-9bb8-ce555a88211f","slug":"editors-picks-2015-part-two","title":"Editors&#8217; Picks 2015: Part Two","excerpt":"<p>Clinical Trial Transparency: disclosures for new drugs below ethical and legal standards In December we surveyed our staff editors, who oversee and manage the peer review process, to tell us about their favourite articles from 2015. Part 2 looks at clinical trial transparency for new drugs.  The ability of medical and healthcare professionals to select [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/05/editors-picks-2015-part-two/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 05, 2016","template":"","format":"standard"}},{"node":{"id":"690a8032-698a-5329-8e7e-b06baedf6503","slug":"editors-picks-2015-part-one","title":"Editors&#8217; Picks 2015: Part One","excerpt":"<p>2015 was another exciting year for BMJ Open, with over 1500 articles published. Since launching in 2011, we have published over 4000 articles, and we look forward to February, when the journal will celebrate its fifth anniversary. In December we surveyed our staff editors, who oversee and manage the peer review process, to tell us about [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/01/04/editors-picks-2015-part-one/\">Read More&#8230;</a></p>\n","status":"publish","date":"January 04, 2016","template":"","format":"standard"}},{"node":{"id":"c6585335-c427-57ad-8080-6241f063c9c5","slug":"the-importance-of-reviewers-2015","title":"The importance of reviewers – 2015","excerpt":"<p>The peer review process is central to scholarly research, a critical part of the publishing process and a method of quality control for the scientific community. While peer review can seem like a daunting, never-ending task at times, without it journals would not survive and continue to publish the important, accurate findings they do today. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/12/23/the-importance-of-reviewers-2015/\">Read More&#8230;</a></p>\n","status":"publish","date":"December 23, 2015","template":"","format":"standard"}},{"node":{"id":"6ed409ca-55cf-57dd-8631-520c43fe2eca","slug":"certain-antidepressants-linked-to-heightened-risk-of-mania-and-bipolar-disorder","title":"Certain antidepressants linked to heightened risk of mania and bipolar disorder","excerpt":"<p>Strongest association found for SSRIs and venlafaxine Taking certain antidepressants for depression is linked to a heightened risk of subsequent mania and bipolar disorder, reveals research published in the online journal BMJ Open. The strongest association seemed to be for serotonin reuptake inhibitors, or SSRIs for short, and the dual action antidepressant venlafaxine, the analysis [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/12/16/certain-antidepressants-linked-to-heightened-risk-of-mania-and-bipolar-disorder/\">Read More&#8230;</a></p>\n","status":"publish","date":"December 16, 2015","template":"","format":"standard"}},{"node":{"id":"8cfa3f26-b5f5-5159-b750-cc0eab29c015","slug":"bicycling-injury-hospitalisation-rates-clinical-trial-registration-and-women-in-leadership-roles-most-read-articles-in-november","title":"Bicycling injury hospitalisation rates, clinical trial registration and women in leadership roles: Most read articles in November","excerpt":"<p>Bicycling injuries, clinical trial registration, and the under-representation of women in leadership roles This month sees a lot of change in the Top 10 Most Read, with many new papers entering the list. At number one this month is Teschke et al&#8216;s assessment of bicycling injury hospitalisation rates with relation to helmet legislation and mode shares, [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/12/09/bicycling-injury-hospitalisation-rates-clinical-trial-registration-and-women-in-leadership-roles-most-read-articles-in-november/\">Read More&#8230;</a></p>\n","status":"publish","date":"December 09, 2015","template":"","format":"standard"}},{"node":{"id":"7cd418a4-b2d9-5cb6-b641-30c82bcd0bfc","slug":"us-pedestrian-wheelchair-users-third-more-likely-to-die-in-car-crashes-than-general-public","title":"US pedestrian wheelchair users third more likely to die in car crashes than general public","excerpt":"<p>And men’s risk is five times higher than women’s, particularly among 50 to 64 year olds Pedestrian wheelchair users in the US are a third more likely to be killed in road traffic collisions than the general public, finds research published in the online journal BMJ Open. And men’s risk is five times higher than women’s, [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/11/19/us-pedestrian-wheelchair-users-third-more-likely-to-die-in-car-crashes-than-general-public/\">Read More&#8230;</a></p>\n","status":"publish","date":"November 19, 2015","template":"","format":"standard"}},{"node":{"id":"2899b338-0363-5c0e-9c69-3f99d02a55b0","slug":"bmj-launches-new-chinese-publishing-portal-qa-with-david-wang-and-huili-chen","title":"BMJ launches new Chinese publishing portal: Q&amp;A with David Wang and Huili Chen","excerpt":"<p>Recently BMJ launched a publishing portal intended to help Chinese authors publish in BMJ&#8217;s portfolio of more than 60 journals, including BMJ Open. To help understand the motivations surrounding the portal, along with some of its content, we asked BMJ China&#8217;s Business Development Manager Huili Chen and the Deputy Editor of BMJ&#8217;s new journal Stroke [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/11/17/bmj-launches-new-chinese-publishing-portal-qa-with-david-wang-and-huili-chen/\">Read More&#8230;</a></p>\n","status":"publish","date":"November 17, 2015","template":"","format":"standard"}},{"node":{"id":"2989b376-f870-5964-a103-8737524946c2","slug":"the-effect-of-statins-obesity-related-behaviours-in-europe-and-the-accuracy-of-wikipedia-content-most-read-articles-in-october","title":"The effect of statins, obesity-related behaviours in Europe and the accuracy of Wikipedia content: Most read articles in October","excerpt":"<p>Statins, obesity-related behaviours, and the accuracy of Wikipedia &nbsp; Several papers have entered the Top 10 Most Read this month, including Kristensen et al&#8216;s systematic review on statins and survival in randomised trials, which tops the list. In our tenth most read article, Azer et al assess the accuracy of content of Wikipedia articles on cardiovascular diseases, and at number [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/11/09/the-effect-of-statins-obesity-related-behaviours-in-europe-and-the-accuracy-of-wikipedia-content-most-read-articles-in-october/\">Read More&#8230;</a></p>\n","status":"publish","date":"November 09, 2015","template":"","format":"standard"}},{"node":{"id":"0ae93a6d-76a2-5159-a76a-cddddeb2dcdc","slug":"oa-journals-and-wikipedia-open-for-collaboration","title":"OA Journals and Wikipedia: Open for collaboration","excerpt":"<p>The theme of this year&#8217;s Open Access week is Open for Collaboration, with the aim of highlighting &#8220;the ways in which collaboration both inspires and advances the Open Access movement&#8221;. Recently BMJ Open published an article by Samy Azer and colleagues investigating whether articles in Wikipedia relating to cardiovascular disease were accurate enough to function as a suitable learning [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2015/10/16/oa-journals-and-wikipedia-open-for-collaboration/\">Read More&#8230;</a></p>\n","status":"publish","date":"October 16, 2015","template":"","format":"standard"}}],"pathPrefix":"posts","first":false,"last":false,"index":10,"pageCount":26,"additionalContext":{}}}

